@import url('http://fonts.googleapis.com/earlyaccess/"Noto Sans KR".css');
div {
  overflow: hidden;
  display: inline-block;
  vertical-align: text-top; }

a {
  display: inline-block;
  vertical-align: text-top;
  text-decoration: none; }

@font-face {
  font-family: 'Godo';
  font-style: normal;
  font-weight: 700;
  src: url("//cdn.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff2") format("woff2"), url("//cdn.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoB.woff") format("woff"); }

.header-box {
  height: 93px;
  width: 100%;
  background-color: #ffffff;
  padding-left: calc((100% - 1047px ) /2);
  padding-right: calc((100% - 1047px ) /2); }

.header-logo-twiti {
  margin-top: 24px;
  width: 103px;
  height: 38px;
  font-family: "Godo";
  font-size: 35px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.14;
  letter-spacing: normal;
  text-align: left;
  color: #b465b4; }

.header-logo-con {
  margin-top: 24px;
  width: 40px;
  height: 45px;
  font-family: "Godo";
  font-size: 40px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.13;
  letter-spacing: normal;
  text-align: left;
  color: #010101; }

.header-menu {
  margin-top: 35px;
  height: 29px;
  font-family: "Noto Sans KR";
  font-size: 20px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.45;
  letter-spacing: normal;
  text-align: left;
  color: #707070; }

.header-basic-emote {
  margin-left: 374px;
  width: 119px; }

.header-usage {
  margin-left: 20px;
  width: 65px; }

.header-twiticon-portal {
  margin-left: 20px;
  width: 137px; }

.header-notice {
  margin-left: 20px;
  width: 77px; }
